*, *:before, *:after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html, body {
  height: 100%;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

.nevi {
  position: fixed;
  width: 100%;
  background-color: #2c3e50;
  padding: 25px 0;
  z-index: 1;
}

.nevi .nevi_wrap {
  position: relative;
}

.nevi a {
  color: #fff;
  padding: 10px;
}

.nevi a:hover {
  color: #18bc9c;
}

.nevi a:focus {
  color: #18bc9c;
}

.nevi .navi-left {
  display: inline-block;
  position: absolute;
  top: 50%;
  transform: translate(0%, -50%);
  font-size: 35px;
}

.nevi .navi-right {
  display: inline-block;
  position: absolute;
  right: 0;
  top: 50%;
  transform: translate(0%, -50%);
  font-size: 22px;
}

.nevi .navi-right li {
  padding-left: 50px;
  display: inline;
}

.nevi_wrap {
  width: 80%;
  height: 60px;
  margin-left: auto;
  margin-right: auto;
}

.navbar-home {
  font-weight: 700;
}

.main_wrap {
  height: 100vh;
  background-color: green;
}

.about {
  height: 550px;
  background-color: rgb(230, 255, 232);
}

.about .about_wrap {
  width: 70%;
  height: 100%;
  margin-left: auto;
  margin-right: auto;
}

.about .about_title {
  text-align: center;
  padding: 100px 0px 50px 0px;
  font-size: 25px;
}

.about .about_content {
  height: 100%;
  padding-bottom: 217px;
}

.about .about_content .about_img {
  display: inline-block;
  width: 50%;
}

.about .about_content .about_img img {
  width: 80%;
  height: 270px;
  border: outset 1px gray;
}

.about .about_content .about_img p {
  width: 80%;
}

.about .about_content .about_text_wrap {
  display: inline-block;
  width: 50%;
  float: right;
}

.about .about_content .about_text_wrap h4 {
  padding-bottom: 5px;
  font-size: 20px;
}

.about .about_content .about_text_wrap p {
  font-size: 14px;
  margin-bottom: 15px;
}

.about .about_content .about_text_wrap li {
  font-size: 14px;
  padding-bottom: 5px;
}

.tech_wrap {
  width: 100%;
  background-color: #fff;
}

.tech_wrap .tech_inner-wrap {
  width: 75%;
  margin-left: auto;
  margin-right: auto;
}

.tech_wrap .tech_inner-wrap .tech_title {
  text-align: center;
  padding: 100px 0px 50px 0px;
  font-size: 25px;
}

.tech_wrap .tech_inner-wrap .tech_list {
  display: flex;
}

.tech_wrap .tech_inner-wrap ul {
  width: 100%;
  height: 100%;
}

.tech_wrap .tech_inner-wrap li {
  width: 25%;
  text-align: center;
  float: left;
  list-style: none;
  padding-bottom: 50px;
}

.tech_wrap .tech_inner-wrap li img{
  width: 150px;
  height: 150px;
  border: none;
}

.tech_wrap .tech_inner-wrap li p {
  font-weight: 500;
  font-size: 25px;
}

.project_wrap {
  height: 100vh;
  background-color: aquamarine;
}